* {
  font-family: "Inter";
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  color: rgb(145, 145, 145);
}

body {
  margin: 1rem;
  font-weight: 300;
  font-style: normal;
  background-color: black;
  color: darkgray;
}

section:hover {
  background-color: #272727;
}

article {
  display: flex;
  align-items: center;
  justify-content: center;
}

article > div {
  margin-right: 0.75rem;
}

.btn_styles {
  margin: 0rem 0.5rem;
  background-color: #272727;
  border: none;
  cursor: grab;
}

.folder {
  display: flex;
  height: 2.25rem;
  margin: 0.5rem 0;
  align-items: center;
  justify-content: space-between;
  padding: 1rem 0.5rem;
  background-color: black;
  border-radius: 0.25rem;
  cursor: pointer;
}

.btn_styles:active {
  cursor: grabbing;
}

.not__selectable {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.input__newFileFolder {
  width: 100%;
  height: 2.25rem;
  background-color: #272727;
  border: 2px solid rgb(46, 46, 46);
  border-radius: 0.25rem;
  outline: none;
  font-size: 1rem;
  padding: 0 0.5rem;
}

.inputbox__styles {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
}
